.error-page{
    @include flex(center, center, column);
    @include layout(100%, 100%);

    .error-img{
        @include layout(650px, auto);
    }
    .error-text{
        @include flex(center, center, column);
        gap: 16px;
        font-size: 28px;        
        font-weight: 700;
    }
}